import React from 'react'
import PropTypes from 'prop-types'
// import MenuIcon from '@material-ui/icons/Menu'
import { Marker } from 'react-map-gl'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles(theme => ({
  root: {
    // border: '1px solid red'
  },
  icon: {
    zIndex: 100,
    width: 20,
    height: 20,
    position: 'absolute',
    top: '50%',
    left: '50%',
    textAlign: 'center',
    transformOrigin: '50% 50%',
    transform: 'translateY(-50%) translateX(-50%)'
  }
}))

const ClickMarker = ({ latLng, Icon, align }) => {
  const classes = useStyles()
  const styleTweaks: any = {}
  if (align) {
    switch (align[0]) {
      case 'bottom':
        styleTweaks.transform = 'translateY(-100%)'
        break
      case 'top':
        styleTweaks.transform = 'translateY(0%)'
        break
      case 'center':
        styleTweaks.transform = 'translateY(-50%)'
        break
      default:
        throw new Error('Must specify vertical placement of align')
    }
    switch (align[1]) {
      case 'left':
        styleTweaks.transform += ' translateX(0%)'
        break
      case 'right':
        styleTweaks.transform += ' translateX(-100%)'
        break
      case 'center':
        styleTweaks.transform += ' translateX(-50%)'
        break
      default:
        throw new Error('Must specify horizontal placement of align')
    }
  }
  return (
    <Marker
      className={classes.root}
      longitude={latLng[0]}
      latitude={latLng[1]}
    >
      <div className={classes.root}>
        <Icon className={classes.icon} style={styleTweaks} />
      </div>
    </Marker>
  )
}

ClickMarker.propTypes = {
  latLng: PropTypes.arrayOf(PropTypes.number).isRequired,
  Icon: PropTypes.object.isRequired,
  align: PropTypes.arrayOf(PropTypes.string)
}

export default ClickMarker
