import React from 'react'
import PropTypes from 'prop-types'
import Paper from '@material-ui/core/Paper'
// import Typography from '@material-ui/core/Typography'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import { util } from '@northarrowresearch/common'
import numeral from 'numeral'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles, Theme, StyleRules } from '@material-ui/core/styles'

const IGNORE_FIELDS = ['ogc_fid', 'enabled', 'wbarea_permanent_identifier', 'visibilityfilter', 'gnis_id', 'gnis_name']
const IDS = [
    'nhdplusid',
    'vpuid',
    'reachcode',
    'permanent_identifier',
    'fromnode',
    'tonode',
    'hydroseq',
    'levelpathi',
    'terminalpa',
    'dnlevelpat',
    'dnhydroseq'
]

const useStyles = makeStyles(
    (theme: Theme): StyleRules => ({
        root: {
            height: '100%',
            overflow: 'scroll',
            '& *': {
                userSelect: 'text'
            }
        },
        table: {
            // display: 'flex',
            // flexDirection: 'column',
            // height: '100%',
            // overflow: 'hidden'
        },
        tableHead: {
            // flex: '0 0'
        },
        tableBody: {
            // flex: '1 1',
            // border: '1px solid red',
            // overflow: 'scroll'
        },
        tableCell: {
            // padding: theme.spacing(1) / 2
        }
    })
)

function dataPrint(k, val): React.ReactNode {
    if (val === null || val === undefined) {
        return <em>n/a</em>
    } else if (IDS.indexOf(k) > -1) return <code>{val}</code>
    else if (util.isNumeric(val)) {
        if (k === 'spheroid_dist') {
            return numeral(val).format('0.00000')
        }
        return util.formatNum(val)
    } else return val.toString()
}

const MarkerFields = ({ data }) => {
    const classes = useStyles()
    return (
        <TableContainer component={Paper} className={classes.root}>
            <Table className={classes.table} aria-label="simple table">
                <TableHead className={classes.tableHead}>
                    <TableRow>
                        <TableCell className={classes.tableCell}>Field</TableCell>
                        <TableCell align="right" className={classes.tableCell}>
                            Value
                        </TableCell>
                    </TableRow>
                </TableHead>
                <TableBody className={classes.tableBody}>
                    {Object.keys(data)
                        .filter((k) => IGNORE_FIELDS.indexOf(k) === -1)
                        .map((k, idx) => (
                            <TableRow key={`${k}-${idx}`}>
                                <TableCell component="th" scope="row" className={classes.tableCell}>
                                    {k.replace(/_/g, ' ')}
                                </TableCell>
                                <TableCell align="right" className={classes.tableCell}>
                                    {dataPrint(k, data[k])}
                                </TableCell>
                            </TableRow>
                        ))}
                </TableBody>
            </Table>
        </TableContainer>
    )
}

MarkerFields.propTypes = {
    data: PropTypes.object.isRequired
}

export default MarkerFields
