import React, { useState, useEffect, useRef } from 'react'
import PropTypes from 'prop-types'
import { useHosted } from '@northarrowresearch/react-auth'
import { api, graphql } from '@netatt/common'
import App from './App'
import log from 'loglevel'
import { endpoints, aws, isDev } from './config'
import { ClickMarkerType } from './types'

const AppConnect = ({ location, history }) => {
    const [loading, setLoading] = useState<boolean>(true)
    const [vectorTiles, setVectorTiles] = useState<any>()
    const [clickMarker, setClickMarker] = useState<ClickMarkerType>()
    const currClickMarkerRef = useRef<ClickMarkerType>()
    currClickMarkerRef.current = clickMarker

    const { isAuthenticated, getCurrentToken, signIn } = useHosted(aws, true, isDev)

    useEffect(() => {
        // RS TILES CDN ==> /NET_ATT/BLM/NHDFlowline/metadata.json
        if (!loading) return
        const tilepath = 'NET_ATT/BLM/NHDFlowline'
        const url = `${process.env.REACT_APP_RS_TILES_CDN}/${tilepath}/metadata.json`
        log.info('URL', url)

        fetch(url)
            .then((res) => res.json())
            .then((d) => {
                d.bounds = JSON.parse(`[${d.bounds}]`)
                d.center = JSON.parse(`[${d.center}]`)
                return {
                    ...d,
                    sourceName: 'NHDFlowline',
                    url: `${process.env.REACT_APP_RS_TILES_CDN}/${tilepath}/{z}/{x}/{y}.pbf`,
                    json: JSON.parse(d.json),
                    zoom: 14,
                    minZoom: parseInt(d.minzoom, 10),
                    maxZoom: parseInt(d.maxzoom, 10),
                    longitude: d.center[0],
                    latitude: d.center[1]
                }
            })
            .then((NHDFlowline) => {
                log.debug('RESP', NHDFlowline)
                setVectorTiles({ ...vectorTiles, NHDFlowline })
                setLoading(false)
            })
            .catch((err) => {
                console.log('DATA::fetch_tile::err:', err) // Print the error if one occurred
            })
    }, [loading, vectorTiles])

    const handleGotRecord = (record: any): void => {
        try {
            log.debug('handleGotRecord:: Record', record)
            const { cp_line_pt: cpLinePt, pt, ...jsonData } = JSON.parse(record.data)
            log.debug('handleGotRecord:: Record data parsed', { cpLinePt, pt, jsonData })
            const newPt = cpLinePt ? JSON.parse(cpLinePt) : undefined

            setClickMarker({
                ...(currClickMarkerRef.current as ClickMarkerType),
                loading: false,
                intLngLat: newPt ? newPt.coordinates : undefined,
                catchGeo: record.catchGeom.bbox ? JSON.parse(record.catchGeom.geom) : undefined,
                catchBBox: record.catchGeom.bbox ? JSON.parse(record.catchGeom.bbox) : undefined,
                reachBBox: record.reachGeom.bbox ? JSON.parse(record.reachGeom.bbox) : undefined,
                jsonData: { nhdplusid: record.id, ...jsonData }
            })
        } catch (e) {
            log.error(e, record)
            setClickMarker({ ...(currClickMarkerRef.current as ClickMarkerType), loading: false })
        }
    }

    const handleMapClick = ({ lngLat }): Promise<void> => {
        setClickMarker({
            lngLat,
            loading: true,
            intLngLat: undefined,
            catchGeo: undefined,
            catchBBox: undefined,
            reachBBox: undefined,
            jsonData: {}
        })
        return getCurrentToken().then((jwt) =>
            api
                .apiCall(endpoints.graphql as string, jwt, graphql.queries.geoRecord, { lngLat })
                .then((response: any) => handleGotRecord(response.data.geoRecord))
        )
    }

    const handleSearchSubmit = (nhdplusid: number): Promise<void> => {
        return getCurrentToken().then((jwt) =>
            api
                .apiCall(endpoints.graphql as string, jwt, graphql.queries.record, { id: nhdplusid })
                .then((response: any) => {
                    if (response.data.record) {
                        setClickMarker({
                            lngLat: undefined,
                            loading: false,
                            intLngLat: undefined,
                            catchGeo: undefined,
                            catchBBox: undefined,
                            reachBBox: undefined,
                            jsonData: {}
                        })
                    }
                    return handleGotRecord(response.data.record)
                })
        )
    }

    if (!isAuthenticated)
        return (
            <div>
                <button onClick={(): Promise<any> => signIn()}>Logged Out</button>
            </div>
        )
    return <App {...{ loading, vectorTiles, clickMarker, handleMapClick, searchSubmit: handleSearchSubmit }} />
}
AppConnect.propTypes = {
    location: PropTypes.object,
    history: PropTypes.object
}

export default AppConnect
