import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import { AppFrame } from '@northarrowresearch/react-common'
import Topbar from './Topbar'
import Map from './Map'
import MarkerFields from './MarkerFields'
import style from './App.style'

const useStyles = makeStyles(style)

interface AppProps {
    loading?: boolean
    vectorTiles?: any
    clickMarker?: any
    searchSubmit: (nhdid: number) => Promise<void>
    handleMapClick: (e: any, MapboxObj: any) => void
}

/**
 * This is the over-arching wrapper for the app. It combines all the
 * necessary pieces
 */
const App: React.FC<AppProps> = ({ loading, vectorTiles, clickMarker, handleMapClick, searchSubmit }: AppProps) => {
    const classes = useStyles()

    if (loading || !vectorTiles) return <div>loading...</div>
    return (
        <AppFrame
            className={classes.appFrame}
            topbar={<Topbar title={'Network Attributes'} searchSubmit={searchSubmit} />}
            fullWidth
            noScroll
        >
            <div className={classes.parentContainer}>
                {clickMarker && clickMarker.jsonData && (
                    <div id="drawer-container" className={classes.drawerContainer}>
                        <MarkerFields data={clickMarker.jsonData} />
                    </div>
                )}
                <div className={classes.mapContainer}>
                    <Map {...{ vectorTiles, clickMarker, handleMapClick }} />
                </div>
            </div>
        </AppFrame>
    )
}

export default App
