import React, { useState, useMemo, useRef, useLayoutEffect } from 'react'
import { FlyToInterpolator } from 'react-map-gl'
import { easeCubic } from 'd3-ease'
import { BaseMap, StyleButtons, MapStyles, combineStyles } from '@northarrowresearch/react-maps'
import { mapConfig } from './config'
import { util } from '@northarrowresearch/common'
import NotListedLocationIcon from '@material-ui/icons/NotListedLocation'
import GpsFixedIcon from '@material-ui/icons/GpsFixed'
import RoomIcon from '@material-ui/icons/Room'
import ClickMarker from './ClickMarker'
import log from 'loglevel'
import { Source, Layer } from 'react-map-gl'

import mapStyle from './App.mapbox'
import { ClickMarkerType } from './types'

interface MapProps {
    vectorTiles?: any
    clickMarker?: ClickMarkerType
    upstreamPoly?: any
    handleMapClick: (e: any, MapboxObj: any) => void
}

const initialView = {
    lngLat: [-114.374749399575, 44.2983897312401]
}

/**
 * This is the over-arching wrapper for the app. It combines all the
 * necessary pieces
 */
const Map: React.FC<MapProps> = ({ vectorTiles, clickMarker, handleMapClick }: MapProps) => {
    const [baseLayer, setBaseLayer] = useState<number>(0)
    const [mapViewport, setMapViewport] = useState<any>(initialView)
    const mapBoxRef = useRef()
    const boundsRef = useRef<number[]>()
    const [isTransitioning, setIsTransitioning] = useState(false)
    const [transitions, setTransitions] = useState<any>()

    const finalStyle = useMemo(() => {
        if (!vectorTiles) return null
        return combineStyles(MapStyles[baseLayer].style, mapStyle(vectorTiles))
    }, [baseLayer, vectorTiles])

    useLayoutEffect(() => {
        if (!clickMarker) return
        if (clickMarker?.reachBBox) {
            if (mapBoxRef.current) {
                const mbRef: any = mapBoxRef.current
                const lineLayer = mbRef.getLayer('NHDFlowlines_lines')
                const lineColor = lineLayer.getPaintProperty('line-color')
                lineColor[2] = [parseInt(clickMarker?.jsonData.nhdplusid, 10)]
                log.debug('line-color', lineColor, lineLayer)
                mbRef.setPaintProperty('NHDFlowlines_lines', 'line-color', lineColor)
            }
        }
        if (clickMarker?.catchBBox || clickMarker?.reachBBox) {
            const newBBox = clickMarker?.catchBBox || clickMarker?.reachBBox
            const newBounds = [
                Math.min(...newBBox.coordinates[0].map((ma): number => ma[0])),
                Math.min(...newBBox.coordinates[0].map((ma): number => ma[1])),
                Math.max(...newBBox.coordinates[0].map((ma): number => ma[0])),
                Math.max(...newBBox.coordinates[0].map((ma): number => ma[1]))
            ]
            if (newBounds.length === 4 && newBounds.every(util.isNumeric)) {
                const newLngLat = [(newBounds[0] + newBounds[2]) / 2, (newBounds[1] + newBounds[3]) / 2]
                if (
                    !boundsRef.current ||
                    !newBounds.every((ma, idx) => !boundsRef.current || ma === boundsRef.current[idx])
                ) {
                    boundsRef.current = newBounds
                    setTransitions({
                        transitionDuration: 2000,
                        transitionInterpolator: new FlyToInterpolator(),
                        transitionEasing: easeCubic
                    })
                    setMapViewport({ bounds: newBounds, lngLat: newLngLat })
                    log.debug('Mapbounds Set', { clickMarker })
                }
            }
        }
    }, [clickMarker])

    const onViewportChange = ({ viewport: { latitude, longitude, zoom }, size, projection, map }) => {
        if (!mapBoxRef.current) mapBoxRef.current = map
        // curZoomRef.current = zoom
        log.debug('zoom', latitude, longitude, zoom)
    }

    const lineSource =
        clickMarker && clickMarker.intLngLat
            ? {
                  type: 'Feature',
                  properties: { percentile: 0 },
                  geometry: {
                      type: 'LineString',
                      coordinates: [clickMarker.lngLat, clickMarker.intLngLat]
                  }
              }
            : null
    const upstreamPolySource =
        clickMarker && clickMarker.catchGeo
            ? {
                  type: 'Feature',
                  properties: {},
                  geometry: clickMarker.catchGeo
              }
            : null

    return (
        <BaseMap
            mapBoxToken={mapConfig.mapBoxToken}
            // lockToBounds
            reactMapGLProps={{
                clickRadius: 2,
                maxZoom: 14,
                minZoom: 6,
                onClick: handleMapClick
            }}
            bounds={mapViewport.bounds}
            showMe
            viewport={{
                zoom: vectorTiles.NHDFlowline.zoom || 12,
                longitude: mapViewport.lngLat[0],
                latitude: mapViewport.lngLat[1]
            }}
            onViewportChange={onViewportChange}
            mapStyles={[{ style: finalStyle }]}
            transitions={transitions}
            onTransitionStart={(): void => {
                log.debug('onTransition START')
                setIsTransitioning(true)
            }}
            onTransitionEnd={(): void => {
                log.debug('onTransition END')
                setIsTransitioning(false)
                setTransitions(null)
            }}
        >
            {clickMarker && clickMarker.lngLat && (
                <ClickMarker
                    latLng={clickMarker.lngLat}
                    Icon={clickMarker.loading ? NotListedLocationIcon : RoomIcon}
                    align={['bottom', 'center']}
                />
            )}
            {clickMarker && clickMarker.intLngLat && <ClickMarker latLng={clickMarker.intLngLat} Icon={GpsFixedIcon} />}
            {lineSource && (
                <Source type="geojson" data={lineSource}>
                    <Layer
                        {...{
                            id: 'data',
                            type: 'line',
                            paint: {
                                'line-color': '#FF0090',
                                'line-opacity': 1,
                                'line-width': 3
                            }
                        }}
                    />
                </Source>
            )}
            {upstreamPolySource && (
                <Source type="geojson" data={upstreamPolySource}>
                    <Layer
                        {...{
                            id: 'upstream_fill',
                            type: 'fill',
                            paint: {
                                'fill-opacity': ['interpolate', ['linear'], ['zoom'], 6, 0.2, 14, 0.2],
                                'fill-color': '#9933ff'
                            }
                        }}
                    />
                </Source>
            )}
            {upstreamPolySource && (
                <Source type="geojson" data={upstreamPolySource}>
                    <Layer
                        {...{
                            id: 'upstream_line',
                            type: 'line',
                            paint: {
                                'line-color': '#9933ff',
                                'line-opacity': 1,
                                'line-width': 3
                            }
                        }}
                    />
                </Source>
            )}
            <StyleButtons
                controlPadding={{}}
                currStyle={baseLayer as number}
                mapStyles={MapStyles}
                setCurrStyle={(idx?: number): void => setBaseLayer(idx as number)}
            />
        </BaseMap>
    )
}

export default Map
